import axios from 'axios'

export function signupUser(lastname, firstname, phone, email, password) {
    return axios.post('https://hospiback.herokuapp.com/api/auth/signup', {
        password: password,
        email: email,
        firstname: firstname,
        lastname: lastname,
        tel: phone
    })
}

export function logUser(password, email) {
    return axios.post('https://hospiback.herokuapp.com/api/auth/login', {
        password: password,
        email: email
    })
}

export function deleteUserById(userId, token) {
    return axios.delete(`https://hospiback.herokuapp.com/api/users/${userId}`, {
        headers: {
            'Authorization': `bearer ${token}`

        }
    })
}

export function updateUserById(userId, firstname, lastname, token) {
    return axios.put(`https://hospiback.herokuapp.com/api/users/${userId}`, {
        firstname: firstname,
        lastname: lastname,


    }, {
        headers: {
            'Authorization': `bearer ${token}`

        }
    })
}


export function getUserById(userId, token) {
    return axios.get(`https://hospiback.herokuapp.com/api/users/${userId}`, {
        headers: {
            'Authorization': `bearer ${token}`

        }
    })
}