<template>
    <div class="container">
    <div class="box">
        <div class="box__border">

                <p class="welcome">Ravie de vous revoir!</p>
               <form action="" class="form_box">
    
                    <div class=" ">
                        <div>
                            <label for="mail" aria-label="Votre adresse email" >Mail*</label>
                            <input v-model="mail" type="email" required>
                        </div>
                            
                        <div>
                            <label for="password" aria-label="Entrer le même mot de passe">Mot de passe*</label>
                            <input v-model="password" type="password" pattern="[ A-Za-z-0-9\p{L}]{2,254}" required>
                        </div>
                    </div>


                
                    <button @click="login" class="center space">Envoyer</button>
             </form>
        </div>
        <div class="box__background"> </div>
    </div>
   </div> 
</template>
<script>
import {logUser} from '../api/users.api'



export default ({
    name:'Login',
    data(){
        return{
            mail:"",
            password:"",

        } 
    }, 
    
    methods:{
            login(e){
                e.preventDefault();
                //POST for login
               logUser( this.password,this.mail)
                .then(response=>{
                    console.log(response)
                    
                    
                    this.$store.state.userId=response.data.userId
                    this.$store.state.admin=response.data.admin
                    this.$store.state.token=response.data.token
                    this.$store.state.refreshToken=response.data.refreshToken

                    

                    console.log(this.$store.state)
                    this.$router.push({path:'/'})
               

                })
                .catch(error=>{
                    console.log(error)
                })


            },
    }
})
</script>

