<template>
    <div class="box">
        
    <form action="" class="form_box">
  
        <div class="row">
            <div class="label_and_input">
                <label for="name" aria-label="Votre nom">Nom*</label>
                <input  v-model="lastname" type="text" pattern="[ A-Za-z-0-9\p{L}]{2,254}" required>
            </div>

            <div class="label_and_input">
                <label for="firstname" aria-label="Votre Prénom">Prénom*</label>
                <input v-model="firstname" type="text" pattern="[ A-Za-z-0-9\p{L}]{2,254}" required>
            </div>
        </div>

        <div class="row ">
            <div class="label_and_input">
                <label for="phone" aria-label="Votre numéro de téléphone">Téléphone*</label>
                <input v-model="phone" type="tel" pattern="[ 0-9\p{L}]{15}" required>
            </div>

            <div class="label_and_input">
                <label for="mail" aria-label="Votre adresse email" >Mail*</label>
                <input v-model="mail" type="email" required>
            </div>
        </div>

        <div class="row">
            <div class="label_and_input">
                <label for="password" aria-label="Entrer un mot de passe">Mot de passe*</label>
                <input  v-model="password" type="password" pattern="[ A-Za-z-0-9\p{L}]{2,254}" required>
            </div>

            <div class="label_and_input">
                <label for="password" aria-label="Entrer le même mot de passe">Confirmer le mot de passe*</label>
                <input v-model="confirmation" type="password" pattern="[ A-Za-z-0-9\p{L}]{2,254}" required>
            </div>
        </div>

      
        <button @click="createUser" class="center space">Envoyer</button>
    </form>
    <img src="../assets/plane.png" alt="un avion en papier" class="form_box__img">
    </div>
</template>
<script>
import {signupUser} from '../api/users.api'

export default ({
    name: 'Signup',
    data(){
        return{
            lastname:"",
            firstname:"",
            phone:"",
            mail:"",
            password:"",
            confirmation:""
        }
    },

    methods:{
            createUser(e){
                e.preventDefault();
                //POST for login
               signupUser(this.lastname, this.firstname, this.phone, this.mail, this.password)
                .then(response=>{
                    console.log(response)
                    
                    
                    this.$store.state.userId=response.data.userId
                    this.$store.state.admin=response.data.admin
                    this.$store.state.token=response.data.token
                    this.$store.state.refreshToken=response.data.refreshToken


                    this.$router.push({path:'/'})
                    console.log("verif store",this.$store.state)


                })
                .catch(error=>{
                    console.log(error)
                })


            },
    }
})
</script>
